.image-container {
    display: block;
    text-align: center;
  }
  
  .series-border {
    display: inline-block;
    padding: 20px;
    border-radius: 20px;
    background: white;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .series-border img {
    display: block;
    max-width: 100%;
    height: auto;
  }